<template>
  <div>
    <div class="container mx-auto p-2">
      <div class="row">
        <div class="col-12 col-md-2">{{num1}}</div>
        <div class="col-12 col-md-2">{{oper}}</div>
        <div class="col-12 col-md-2">{{num2}}</div>
        <div class="col-12 col-md-2">=</div>
        <div class="col-12 col-md-4">
          <div class="form-group">
            <input ref="digite" v-model="resultado" class="form-control" type="number" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import {mapState, mapActions} from 'vuex'

export default {
    name: 'lalaa',
  props: {
    num1: Number,
    num2: Number,
    oper: String,
    resu: Number
  },
  data(){
      return{
          resultado: null,
          res1: "",
          startDate: null,
          endDate: null,
      }
  },
  methods:{
      ...mapActions(['confirm']),
      verify(){
          if(this.resu==this.resultado){
              this.endDate = new Date().getTime()
              this.$store.state.duracao = (this.endDate - this.startDate) / 1000
              this.res1 = "Certo"
              this.resultado = null
              this.$refs.digite.focus()
              this.confirm()
              this.startDate = new Date().getTime()
          }else{
              this.res1 = "Errado"
          }
      }
  },
  mounted(){
      this.startDate = new Date().getTime()
      setInterval(() => {
        this.verify()
      }, 100);
  }
};
</script>
<style lang="scss" scoped>
    div{
        font-size: 3.5rem;
    }
    input{
        font-size: 3.5rem;
        text-align: center;
    }
</style>
