import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    nome: '',
    idade: '',
    valor1: "",
    valor2: "",
    operacao: "",
    resultado: "",
    duracao: "",
    historico:[]
  },
  mutations: {
    confirmar(state){
      state.historico.push([state.nome, state.idade, state.valor1,state.valor2,state.operacao,state.resultado,state.duracao])
      state.valor1 = Math.ceil(Math.random()*100)-1

      let oper = ["+","-"][Math.round(Math.random())]
      
      if(oper=="-"){
        state.valor2 = Math.ceil(Math.random()*100)-1
        while(state.valor2>state.valor1){
          state.valor2 = Math.ceil(Math.random()*100)-1
        }
      }else{
        state.valor2 = Math.ceil(Math.random()*100)-1
      }
      
      state.operacao = oper
      let res = 0
      switch (oper) {
        case "+":
          res = state.valor1 + state.valor2
          break;
        case "-":
          res = state.valor1 - state.valor2
          break;
      }
      state.resultado = res
    }
  },
  actions: {
    confirm({commit}){
      commit('confirmar')
    }

  },
  modules: {
  }
})
