<template>
  <div>
    <h1>Teste</h1>
    <br />
    <div v-if="start">
      <form>
        <div class="form-group">
          <label for>Nome</label>
          <input v-model="nome1" type="text" class="form-control" />
        </div>
        <div class="form-group">
          <label for>Idade</label>
          <input v-model="idade1" type="Number" class="form-control" />
        </div>
      </form>
      <button @click="startar()" class="btn btn-success">Começar</button>
    </div>
    <estrutura v-else :num1="valor1" :num2="valor2" :oper="operacao" :resu="resultado" />

    <!-- <ul>
      <li v-for="cada in historico" :key="Math.random()">{{cada}}</li>
    </ul> -->
  </div>
</template>

<script>
import estrutura from "./Estrutura";
import { mapState, mapActions } from "vuex";
import axios from 'axios'

export default {
  name: "testando",
  components: {
    estrutura
  },
  props: {},
  data() {
    return {
      start: true,
      nome1: null,
      idade1: null
    };
  },
  computed: {
    ...mapState([
      "nome",
      "idade",
      "valor1",
      "valor2",
      "operacao",
      "resultado",
      "duracao",
      "historico"
    ])
  },
  methods: {
    ...mapActions(["confirm"]),
    startar() {
      this.start = false;
      this.$store.state.nome = this.nome1
      this.$store.state.idade = this.idade1
      this.confirm();
    },
    async salvar(){
        var dados = this.$store.state.historico
        dados = dados.filter((x)=>{
            return x[2]!==""
        })
        let url = 'https://script.google.com/macros/s/AKfycbzH9aBo6DDue65j1k-kcM8o6KMA9hWbQUCWlixy8dJtqkCjROQ/exec'
        let data = { func: 'save', dados: dados }
        if(dados[0]!==undefined){
            // console.log(dados)
            // console.log("entrou no if")
            await axios.post(url, JSON.stringify(data))
            this.$store.state.historico = []
            // console.log("Passou aqui")
        }else{
            // console.log("entrou no else")
        }
    }
  },mounted(){
      setInterval(() => {
          this.salvar()
      }, 1000);
  }
};
</script>
<style lang="scss" scoped>
div {
  font-size: 2rem;
}
input {
  font-size: 2rem;
  text-align: center;
}
</style>